.box{
    width: 900px;
    height: 45px;
    background: #fff;
    border-top: 1px solid #f0f0f0;
    /* overflow: hidden; */
}
.box img{
    width:35px;
    height: 35px;
    padding-top:10px;
    padding-left: 10px;
    /* border-radius: 5px; */
}
.box a{
    color:black;
    margin-top:-20px;
}
.but{
    width:30px;
    height:20px;
    border:1px solid #d3d3d3;
    border-radius: 3px;
    color:#8d8c8c;
    background-color: #d3d3d3;
    line-height: 16px;
    margin-left: 110px;
    margin-top: -15px;  
}
.text{
    width:700px;
    margin-left: 150px;
    /* margin-top: -20px;   */
}

.visit{
    /* background-color: cadetblue; */
    /* width: 70px; */
    font-size:10px;
    height: 20px;
    margin-left: 50px;
    margin-top:-25px;
    color:#d3d3d3;
}
.visit span{
    font-size: 16px;
    color:#503a55;
}
.box:hover{
    background-color: #e7e5e5;
}
.box a:hover{
    text-decoration:underline;
}

.index{
    height: 50px;
    background-color: #fff;   
     
}
.index ul{
    float: left;
    width: 302px;
    height: 31px;
    border:1px solid rgb(189, 188, 188);
    /* border-radius: 5px; */
    margin-top:10px;
}
.index li{
    display: inline-block;
    width: 30px;
    text-align: center;
    line-height: 30px;
    border-right: 1px solid rgb(189, 188, 188);
}
.sub{
    float: left;
    height: 31px;
    width: 30px;
    text-align: center;
    line-height: 30px;
    border: 1px solid rgb(189, 188, 188);
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    margin-top:10px;
    margin-left: 10px;
}
.add{
    float: left;
    height: 31px;
    width: 30px;
    text-align: center;
    line-height: 30px;
    border: 1px solid rgb(189, 188, 188);
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    margin-top:10px;
}
.fromnow{
    float: right;
    margin-top:-10px;
    margin-right: 10px;
}